const gallery = document.querySelector(".gallery");

gallery.classList.add("gallery")


gallery.insertAdjacentHTML("beforeend", createMarkup(images))

function createMarkup(arr){
    return arr.map(img => 
    `
    <li class="gallery-item">
	<a class="gallery-link" href="${img.original}">
		<img 
			class="gallery-image" 
			src="${img.preview}" 
			alt="${img.description}" 
			/>
	</a>
    </li>

    `).join("")
}

const galleryLinks = document.querySelectorAll(".gallery-link");

galleryLinks.forEach(link => {
    link.addEventListener("click", handelClick);
});

function handelClick(event){
    event.preventDefault()
}


let galleryImages = new SimpleLightbox('.gallery a', {
    showCounter: false,
    captionsData: "alt",
    captionDelay:250

});




const imgElement = document.querySelectorAll(".gallery-image");
imgElement.forEach(element => {
    element.classList.add("gallery_img")
})

const galleryItems = document.querySelectorAll(".gallery-item");

galleryItems.forEach(element => {
    element.classList.add("gallery-item")
})




